.onlineEditor {
  border-radius: 10px;
  box-shadow: 0 0 10px 0 grey;
  padding: 20px;
  background-color: white;
  margin-bottom: 20px;
  transition: 0.3s;
  max-width: 1000px;
  box-sizing: border-box;
}

.time {
  margin-bottom: 10px;
  font-size: 12px;
  color: lightgray;
}

.buttons {
  display: flex;
  justify-content: flex-end;
}

